import { Component, OnInit } from '@angular/core';
import {ConstantsService, PRIVILEGES} from '../services/constants.service';
import {ControllerService} from '../services/controller.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-admin-orders',
  templateUrl: './admin-orders.page.html',
  styleUrls: ['./admin-orders.page.scss'],
})
export class AdminOrdersPage implements OnInit {

  orders: any[] = Object.values(this.api.getOrders());

  constructor(private api: ControllerService, private router: Router, private currency: ConstantsService) { }

  ngOnInit() {
    const user = this.api.getCurrentUser();
    if (user !== PRIVILEGES.ADMIN) {
      this.router.navigateByUrl('/home');
    }
  }

  logout() {
    this.api.logout(PRIVILEGES.ADMIN);
    location.assign('/home');
  }
}
